import React from 'react';
import {ChartData} from './Data';
import { BarChart, Bar, XAxis, YAxis, Tooltip, CartesianGrid, ResponsiveContainer } from 'recharts';


const Cereals1 = () => {
    const data = [
        {name: 'January', uv: ChartData.food.product1.brand1.january},
        {name: 'February', uv: ChartData.food.product1.brand1.february},
        {name: 'March', uv: ChartData.food.product1.brand1.march},
        {name: 'April', uv: ChartData.food.product1.brand1.april},
    ];

    return (
        <div>
            <ResponsiveContainer width='100%' aspect={4.0/1.1}>
                <BarChart className="chart-size" width={1000} height={400} data={data}>
                    <XAxis dataKey="name" stroke="#95ceff" />
                    <YAxis tickCount={7}/>
                    <Tooltip />
                    <CartesianGrid stroke="#ccc" strokeDasharray="0" />
                    <Bar dataKey="uv" fill="#95ceff" barSize={200} />
                </BarChart>
            </ResponsiveContainer>
        </div>
    )
}
