import React from 'react'
import $ from 'jquery'
import {
    Cereals1,
    Cereals2,
    Soda1,
    Soda2,
    Jeans1,
    Jeans2,
    Shoes1,
    Shoes2,
    Phone1,
    Phone2,
    Computer1,
    Computer2
} from './data-components/TheData'


const GetData = () => {

    $(document).ready(function(){
        $('#cereals2').hide();
        $('#soda1').hide();
        $('#soda2').hide();
        $('#jeans1').hide();
        $('#jeans2').hide();
        $('#shoes1').hide();
        $('#shoes2').hide();
        $('#phone1').hide();
        $('#phone2').hide();
        $('#computer1').hide();
        $('#computer2').hide();
    });

    $(document).ready(function () {
        $("#categoria").change(function () {
            var val = $(this).val();
            if (val == "Food") {
                $("#product").html("<option value='Cereals'>Cereals</option><option value='Soda'>Soda</option>");
                $('#cereals1').show();
                $('#cereals2').hide();
                $('#soda1').hide();
                $('#soda2').hide();
                $('#jeans1').hide();
                $('#jeans2').hide();
                $('#shoes1').hide();
                $('#shoes2').hide();
                $('#phone1').hide();
                $('#phone2').hide();
                $('#computer1').hide();
                $('#computer2').hide();
            } else if (val == "Clothes") {
                $("#product").html("<option value='Jeans'>Jeans</option><option value='Shoes'>Shoes</option>");
                $('#cereals1').hide();
                $('#cereals2').hide();
                $('#soda1').hide();
                $('#soda2').hide();
                $('#jeans1').show();
                $('#jeans2').hide();
                $('#shoes1').hide();
                $('#shoes2').hide();
                $('#phone1').hide();
                $('#phone2').hide();
                $('#computer1').hide();
                $('#computer2').hide();
            } else if (val == "Items") {
                $("#product").html("<option value='Phone'>Phone</option><option value='Computers'>Computers</option>");
                $('#cereals1').hide();
                $('#cereals2').hide();
                $('#soda1').hide();
                $('#soda2').hide();
                $('#jeans1').hide();
                $('#jeans2').hide();
                $('#shoes1').hide();
                $('#shoes2').hide();
                $('#phone1').show();
                $('#phone2').hide();
                $('#computer1').hide();
                $('#computer2').hide();
            }
        });
        $("#product").change(function () {
            var val = $(this).val();
            if (val == "Cereals") {
                $("#brand").html("<option value='Cereals1'>Cereals1</option><option value='Cereals2'>Cerelas2</option>");
                $('#cereals1').show();
                $('#cereals2').hide();
                $('#soda1').hide();
                $('#soda2').hide();
                $('#jeans1').hide();
                $('#jeans2').hide();
                $('#shoes1').hide();
                $('#shoes2').hide();
                $('#phone1').hide();
                $('#phone2').hide();
                $('#computer1').hide();
                $('#computer2').hide();
            } else if (val == "Soda") {
                $("#brand").html("<option value='Soda1'>Soda1</option><option value='Soda2'>Soda2</option>");
                $('#cereals1').hide();
                $('#cereals2').hide();
                $('#soda1').show();
                $('#soda2').hide();
                $('#jeans1').hide();
                $('#jeans2').hide();
                $('#shoes1').hide();
                $('#shoes2').hide();
                $('#phone1').hide();
                $('#phone2').hide();
                $('#computer1').hide();
                $('#computer2').hide();
            } else if (val == "Jeans") {
                $("#brand").html("<option value='Jeans1'>Jeans1</option><option value='Jeans2'>Jeans2</option>");
                $('#cereals1').hide();
                $('#cereals2').hide();
                $('#soda1').hide();
                $('#soda2').hide();
                $('#jeans1').show();
                $('#jeans2').hide();
                $('#shoes1').hide();
                $('#shoes2').hide();
                $('#phone1').hide();
                $('#phone2').hide();
                $('#computer1').hide();
                $('#computer2').hide();
            } else if (val == "Shoes") {
                $("#brand").html("<option value='Shoes1'>Shoes1</option><option value='Shoes2'>Shoes2</option>");
                $('#cereals1').hide();
                $('#cereals2').hide();
                $('#soda1').hide();
                $('#soda2').hide();
                $('#jeans1').hide();
                $('#jeans2').hide();
                $('#shoes1').show();
                $('#shoes2').hide();
                $('#phone1').hide();
                $('#phone2').hide();
                $('#computer1').hide();
                $('#computer2').hide();
            } else if (val == "Phone") {
                $("#brand").html("<option value='Phone1'>Phone1</option><option value='Phone2'>Phone2</option>");
                $('#cereals1').hide();
                $('#cereals2').hide();
                $('#soda1').hide();
                $('#soda2').hide();
                $('#jeans1').hide();
                $('#jeans2').hide();
                $('#shoes1').hide();
                $('#shoes2').hide();
                $('#phone1').show();
                $('#phone2').hide();
                $('#computer1').hide();
                $('#computer2').hide();
            } else if (val == "Computers") {
                $("#brand").html("<option value='Computer1'>Computer1</option><option value='Computer2'>Computer 2</option>");
                $('#cereals1').hide();
                $('#cereals2').hide();
                $('#soda1').hide();
                $('#soda2').hide();
                $('#jeans1').hide();
                $('#jeans2').hide();
                $('#shoes1').hide();
                $('#shoes2').hide();
                $('#phone1').hide();
                $('#phone2').hide();
                $('#computer1').show();
                $('#computer2').hide();
            }
        });
        $("#brand").change(function () {
            var val = $(this).val();
            if($('#brand').val().length < 1){
                $('#cereals1').hide();
                $('#cereals2').hide();
            } else{
                if (val == "Cereals1"){
                    $('#cereals1').show();
                } else {
                    $('#cereals1').hide();
                }
                if (val == "Cereals2"){
                    $('#cereals2').show();
                } else {
                    $('#cereals2').hide();
                }
                if (val == "Soda1"){
                    $('#soda1').show();
                } else {
                    $('#soda1').hide();
                }
                if (val == "Soda2"){
                    $('#soda2').show();
                } else {
                    $('#soda2').hide();
                }
                if (val == "Jeans1"){
                    $('#jeans1').show();
                } else {
                    $('#jeans1').hide();
                }
                if (val == "Jeans2"){
                    $('#jeans2').show();
                } else {
                    $('#jeans2').hide();
                }
                if (val == "Shoes1"){
                    $('#shoes1').show();
                } else {
                    $('#shoes1').hide();
                }
                if (val == "Shoes2"){
                    $('#shoes2').show();
                } else {
                    $('#shoes2').hide();
                }
                if (val == "Phone1"){
                    $('#phone1').show();
                } else {
                    $('#phone1').hide();
                }
                if (val == "Phone2"){
                    $('#phone2').show();
                } else {
                    $('#phone2').hide();
                }
                if (val == "Computer1"){
                    $('#computer1').show();
                } else {
                    $('#computer1').hide();
                }
                if (val == "Computer2"){
                    $('#computer2').show();
                } else {
                    $('#computer2').hide();
                }
            }
        });
        
    });
    
    return (
        <>
        <div className="options">
            <div className="col">
                <label for="Category">Category: </label>
                <select id="categoria">
                    <option value="Food">Food</option>
                    <option value="Clothes">Clothes</option>
                    <option value="Items">Items</option>
                </select>
            </div>
            <div className="col">
                <label for="Product">Product: </label>
                <select id="product">
                    <option value="Cereals">Cereals</option>
                    <option value="Soda">Soda</option>
                </select>
            </div>
            <div className="col">
                <label for="Brand">Brand: </label>
                <select id="brand">
                    <option value="Cereals1">Cereals1</option>
                    <option value="Cereals2">Cereals2</option>
                </select>
            </div>
        </div>
        <div className="text">
            <h2>Sales By Month for:</h2>
        </div>
        <div className="chart" id="cereals1">
            <Cereals1 />
        </div>
        <div className="chart" id="cereals2">
            <Cereals2 />
        </div>
        <div className="chart" id="soda1">
            <Soda1 />
        </div>
        <div className="chart" id="soda2">
            <Soda2 />
        </div>
        <div className="chart" id="jeans1">
            <Jeans1 />
        </div>
        <div className="chart" id="jeans2">
            <Jeans2 />
        </div>
        <div className="chart" id="shoes1">
            <Shoes1 />
        </div>
        <div className="chart" id="shoes2">
            <Shoes2 />
        </div>
        <div className="chart" id="phone1">
            <Phone1 />
        </div>
        <div className="chart" id="phone2">
            <Phone2 />
        </div>
        <div className="chart" id="computer1">
            <Computer1 />
        </div>
        <div className="chart" id="computer2">
            <Computer2 />
        </div>
        </>
    )
}

export default GetData
